  width: 100%;
  height: 100%;
  .mask-in {
    position: absolute;
    min-width: 600rpx;
    top: 50%;
    left: 50%;
    background-color: #fff;
    transform: translate(-50%, -50%);
    text {
      color: #ff6501;
    }

  }
  button {
    padding: 0;
    margin: 0;
    line-height: 50rpx;
    font-size: 30rpx;
    background-color: transparent;
    border: none;
    &:after {
