.wave,
.wave1,
.wave2 {
  position: absolute;
  bottom: -160%;
  width: 750rpx;
  height: 750rpx;
  //background-color: hsla(216, 100%, 70%, 0.3);
  background-color: rgb(84, 151, 255);
  border-radius: 45%;
  animation: rotate 6s linear infinite;
  // mix-blend-mode: multiply;
}

.wave1 {
  background-color:rgb(84, 151, 255);
  transform: translate(-20%, 0) rotate(0deg);
  animation: rotate1 6s linear infinite;
}

.wave2 {
  background-color:rgb(84, 151, 255);
  transform: translate(20%, 0) rotate(0deg);
  animation: rotate2 6s linear infinite;
}

@keyframes rotate {
  50% {
    border-radius: 38%;
    transform: rotate(180deg);
  } 100% {
    border-radius: 45%;
  }
}

@keyframes rotate1 {
  0% {
    border-radius: 45%;
    transform: translate(-20%, 0) rotate(0deg);
  }
  50% {
    border-radius: 38%;
    transform: translate(-30%, 8%) rotate(180deg);
  }100% {
     border-radius: 45%;
     transform: translate(-20%, 0%) rotate(360deg);
   }
}

@keyframes rotate2 {
  0% {
    transform: translate(20%, 0) rotate(0deg);
  }
  50% {
    transform: translate(30%, 8%) rotate(180deg);
  }100% {
     transform: translate(20%, 0%) rotate(360deg);
   }
}


//动画
.animated {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

.animated.zoomIn {
  webkit-animation-duration: 0.5s;
  animation-duration: 0.5s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}
.animated.infinite {
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
}

.animated.hinge {
  -webkit-animation-duration: 2s;
  animation-duration: 2s;
}

.animated.bounceIn,
.animated.bounceOut {
  -webkit-animation-duration: .75s;
  animation-duration: .75s;
}

.animated.flipOutX,
.animated.flipOutY {
  -webkit-animation-duration: .75s;
  animation-duration: .75s;
}

.rotateIn {
  -webkit-animation-name: rotateIn;
  animation-name: rotateIn;
}
@keyframes rotateIn {
  from {
    -webkit-transform-origin: center;
    transform-origin: center;
    -webkit-transform: rotate3d(0, 0, 1, -200deg);
    transform: rotate3d(0, 0, 1, -200deg);
    opacity: 0;
  }

  to {
    -webkit-transform-origin: center;
    transform-origin: center;
    -webkit-transform: none;
    transform: none;
    opacity: 1;
  }
}

.fadeIn {
  -webkit-animation-name: fadeIn;
  animation-name: fadeIn;
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.fadeInDown {
  -webkit-animation-name: fadeInDown;
  animation-name: fadeInDown;
}
@keyframes fadeInDown {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}

.zoomIn {
  -webkit-animation-name: zoomIn;
  animation-name: zoomIn;
}
@keyframes zoomIn {
  from {
    opacity: 0;
    -webkit-transform: scale3d(.3, .3, .3);
    transform: scale3d(.3, .3, .3);
  }

  100% {
    opacity: 1;
  }
}